import React from 'react';
import styles from './Article.module.css';

const page = () => {
  return (
    <div className={styles.article}>
        <h3 className={styles.subtitle}>Présentation de l'entreprise Troisetplus</h3>
        <div className={styles.paragraph_100}>
          <p>
              Troisetplus est une agence de communication globale fondée en 2010 par Thierry Beaujean, Kader Rochdi et Nicolas Sanchez après leur expérience commune chez Reymann Communication. Leur objectif était de créer une agence offrant des services innovants et personnalisés.
          </p>
          <p>
              Troisetplus propose une gamme variée de services de communication, comprenant des conseils en communication, des stratégies en ligne et hors ligne, la refonte ou la création d'identités visuelles, ainsi que des conceptions print ou numériques incluant des sites web et leur référencement. Leurs clients proviennent de différents secteurs, allant des grandes entreprises multinationales aux professionnels indépendants.
          </p>
          <p>
              Troisetplus accorde également une grande importance à la transmission de son savoir-faire, proposant des stages réguliers dans divers domaines : gestion de projet, rédaction, graphisme, web design et développement. Les stagiaires sont activement impliqués pour acquérir des compétences pratiques tout en découvrant le monde professionnel.
          </p>
          <p>
              Pour en savoir plus sur leurs services et opportunités, vous pouvez visiter leur site web : <a href="https://troisetplus.fr/" target="_blank" rel="noopener noreferrer">Troisetplus</a>.
          </p>
          
        </div>

        <h3 className={styles.subtitle}>Sujet du Stage</h3>
        <div className={styles.paragraph_100}>
          <p>
              Pendant mon stage, j'ai eu l'occasion de participer à divers projets de développement web. Mes missions principales ont inclus :
          </p>
          <ul>
              <li>
                  Développement de sites internet en collaboration avec les équipes de l'agence pour respecter les délais et les spécifications des projets.
              </li>
              <li>
                  Veille technologique pour rester à jour sur les dernières tendances et technologies du développement web.
              </li>
              <li>
                  Choix des technologies adaptées pour chaque projet en fonction des besoins des clients et des contraintes techniques.
              </li>
              <li>
                  Programmation des fonctionnalités des sites web, incluant la création d'interfaces interactives et la gestion des bases de données.
              </li>
          </ul>
          <p>
              Un des projets majeurs sur lequel j'ai travaillé était le site web d'Alsaflooring, que j'ai développé principalement avec WordPress. J'ai transformé une maquette en un site fonctionnel en concevant la structure, intégrant le contenu et configurant les fonctionnalités spécifiques.
          </p>
          <p>
              J'ai également contribué à un autre projet pour Atelier H, en utilisant les mêmes compétences de développement web.
          </p>
          
        </div> 
        <h3 className={styles.subtitle}>Travail Réalisé sur le Site Alsaflooring</h3>

        <a href="https://www.alsaflooring.com" target="_blank" rel="noopener noreferrer" className={`${styles.paragraph_100} ${styles.margincontent}`}>
            <img className={styles.paragraph_100} src={process.env.PUBLIC_URL + '/data/images/Alsaflooring.png'} alt="Aperçu du site Alsaflooring" />
        </a>

        <p>
            Pendant mon stage, j'ai eu l'opportunité de contribuer à divers aspects du développement du site Alsaflooring. Voici un aperçu des principales tâches réalisées et des solutions mises en place :
        </p>

        <h4 className={styles.paragraph_100}>Générateur de PDF</h4>

        <div className={styles.paragraph_50}>
          <p>
             Une des premières tâche qui ma était confié lors de mon stage, était la conceptions d'un générateur de pdf dynamique en fonction du choix de l'utilisateur.
          </p>
          <p>
              La première étape a consisté à rechercher la solution la plus adaptée pour la génération de PDF à partir de contenu HTML. Après avoir évalué plusieurs options, <strong>html2pdf.js</strong> a été sélectionnée en raison de sa flexibilité et de ses capacités.
          </p>
          <p>
              j"ai donc développé un script JavaScript pour extraire les éléments nécessaires à partir des balises HTML en utilisant leurs identifiants (IDs). Ces éléments contenaient les données cruciales du calculateur, telles que les dimensions, les matériaux et les résultats des calculs. Ensuite, un nouvel HTML a été généré spécifiquement pour le PDF, intégrant les styles CSS nécessaires pour préserver la mise en page et l'apparence de la maquette initiale.
          </p>

        </div>

        <div className={`${styles.paragraph_50} ${styles.margincontent}`}>
            <img className={styles.paragraph_50} src={process.env.PUBLIC_URL + '/data/images/PDFgen.png'} alt="PDFgen.png"></img>
        </div>

        <div className={styles.paragraph_100}>
          <p>
              En intégrant <strong>html2pdf.js</strong> dans le script, la conversion du nouvel HTML en un fichier PDF téléchargeable a été rendue possible. Les options ont été configurées pour ajuster la taille, l'orientation et la qualité du PDF, garantissant un document conforme aux spécifications prévues.
          </p>
          <p>
              Le choix de JavaScript pour cette tâche s'explique par sa facilité d'intégration, notamment via un lien CDN ou une URL externe, ce qui simplifie la maintenance comparée à une solution PHP nécessitant une installation sur le serveur.
          </p>
          <p>
              Pour améliorer l'expérience utilisateur pendant la génération du PDF, j'ai réalisé une simple animation de chargement pour indiquer que le processus était en cours. Cette animation, réalisée avec CSS et JavaScript, a fourni un élément visuelle essentielle, réduisant ainsi la confusion et la frustration liées au temps de traitement de génération du PDF.
          </p>
        </div>

        

        <h4 className={styles.paragraph_100}>Custom Post Types</h4>
        <div className={styles.paragraph_50}>
          <p>
              Dans le cadre du développement du site, plusieurs aspects liés aux <strong>custom post types</strong> et aux taxonomies ont été abordés pour améliorer l'organisation et la gestion du contenu :
          </p>
          <p>
              Les <strong>custom post types</strong> sont des contenus personnalisés permettent d'étendre les fonctionnalités de WordPress au-delà des articles et pages standards. Pour Alsaflooring, les custom post types suivants ont été développés : stratifié, parquet, commercial, revendeur, emploi, et accessoire. Cette approche a permis d'organiser le contenu de manière plus structurée et adaptée aux besoins spécifiques du site.
          </p>
        </div>

        <div className={styles.paragraph_50}>
            <img className={styles.paragraph_100} src={process.env.PUBLIC_URL + '/data/images/Stratifiée.png'} alt="Stratifiée.png"></img>
        </div>

        <div className={styles.paragraph_100}>
          <p>
              La configuration de ces custom post types a été effectuée en utilisant du code PHP, assurant leur intégration fluide au site. Les intégrateurs peuvent désormais ajouter et gérer facilement les contenus associés à chaque type, sans nécessiter de configurations complexes. Cette flexibilité facilite la gestion du site et optimise l'affichage du contenu.
          </p>
          <p>
              Parallèlement, des <strong>taxonomies</strong> ont été mises en place pour affiner la classification du contenu. Les taxonomies permettent de regrouper les contenus en fonction de critères spécifiques. Par exemple, une taxonomie appelée "couleur" a été créée pour classifier les parquets en fonction de leurs caractéristiques telles que gris, naturel, etc. Cette structuration permet aux utilisateurs de naviguer plus facilement entre les différents types de contenus et de trouver rapidement les informations recherchées.
          </p>
          <p>
              En ajoutant ces taxonomies, l'organisation générale du site a été améliorée, rendant la gestion du contenu plus efficace et la navigation plus intuitive pour les utilisateurs.
          </p>
        </div>

        <h4 className={styles.paragraph_100}>Mise en place d’ACF</h4>
        <div className={styles.paragraph_100}>
            <p>
                La mise en place d'Advanced Custom Fields (ACF) a été essentielle pour le site Alsaflooring. ACF est une extension de WordPress qui permet d’ajouter des champs personnalisés à divers types de contenus tels que les articles, les pages, les custom post types, et les taxonomies. Cela permet de gérer et d’afficher des informations au-delà du contenu standard proposé par WordPress.
            </p>
            <p>
                L'intégration d'ACF a permis de créer des blocs et des champs personnalisés pour structurer le contenu de manière plus adaptée aux besoins du site. Par exemple, des blocs de titre ont été créés pour organiser les différentes sections du site. ACF a également été utilisé pour afficher des contenus spécifiques, comme des articles relatifs aux différents types de parquets (stratifié, parquet, accessoire), en établissant des relations entre ces contenus.
            </p>
            <p>
                La création de champs ACF a permis une gestion plus précise et personnalisée du contenu en les associant directement à des pages ou à des termes de taxonomies qui permettent d'enrichir les pages avec des informations spécifiques.
            </p>
            <p>
                En ce qui concerne les templates, leur création et personnalisation ont été réalisées pour définir la structure et la mise en page des différents types de contenus. Les templates ont été conçus pour intégrer les informations fournies par les champs ACF, offrant ainsi une présentation cohérente et structurée des données sur le site.
            </p>
            <p>
                L’utilisation d’ACF permet considérablement d'amélioré la flexibilité de la gestion du contenu pour les administrateurs. Les champs personnalisés facilite la saisie et l'affichage des informations, rendant le processus de gestion plus intuitif sans nécessiter d’interventions directes dans le code source du site.
            </p>
        </div>

        <h4 className={styles.paragraph_100}>Algorithme de Recherche Dynamique</h4>
     
        <div className={styles.paragraph_100}>
            <p>
                Un autre élement que j'ai développer , est un algorithme de recherche dynamique qui permet aux utilisateurs d'effectuer des recherches avancées sans recharger la page. Cet algorithme utilise PHP, JavaScript (via jQuery), et AJAX pour offrir une expérience utilisateur fluide.
            </p>
            <p>
                Le fonctionnement de l'algorithme est le suivant : lorsque l'utilisateur entre des termes de recherche dans un champ dédié, ces informations sont envoyées au serveur via une requête AJAX. Le serveur utilise ensuite ces termes pour effectuer une recherche dans la base de données, en prenant en compte des critères spécifiques comme les couleurs ou les types de produits.
            </p>
        </div>

        <div className={styles.paragraph_50}>
            <img className={styles.paragraph_100} src={process.env.PUBLIC_URL + '/data/images/recherchePose.png'} alt="recherchePose.png"></img>
        </div>

        <div className={`${styles.paragraph_50} ${styles.padding1em}`}>
            <p>
                Pour la mise en œuvre de cet algorithme, des requêtes personnalisées en PHP ont été utilisées pour interroger la base de données et récupérer les résultats correspondant aux termes de recherche. Des fonctions de tri ont également été employées pour organiser les résultats en fonction de leur pertinence et de leur date de mise à jour.
            </p>
            <p>
                Sur le front-end, jQuery a été utilisé pour capturer les événements de saisie de l'utilisateur et envoyer les termes de recherche au serveur via une requête AJAX. Les résultats renvoyés par le serveur sont ensuite affichés dynamiquement sur la page en utilisant JavaScript, sans recharger la page entière.
            </p>
        </div>

        <h4 className={styles.paragraph_100}>Autres Contributions Clés au Projet</h4>
        <div className={styles.paragraph_100}>
            <p>
                j'ai développé bien autres éléments techniques, voici un aperçu des autres aspects importants que j'ai pris en charge :
            </p>

            <ul>
              <li>
              <strong>Partage du Thème WordPress :</strong>J'ai mis en place un dépôt GitHub pour partager le thème WordPress, assurant que toute l'équipe puisse accéder aux versions les plus récentes et collaborer efficacement. Pour le déploiement, j'ai utilisé le plugin "All-in-One WP Migration" pour transférer le thème et la base de données vers le serveur de production.
              </li>
              <li>
              <strong>Mise en Place des Pages :</strong>J’ai contribué à la création et à la mise en place des pages du site, comme la page d'accueil, la page des produits et la documentation. J'ai utilisé HTML, CSS, et les fonctionnalités de WordPress pour garantir une présentation conforme aux maquettes et responsive.
              </li>
            </ul>
        </div>

      <h3 className={styles.subtitle}>Projet Atelier H</h3>

      <a href="http://atelierh-alsace.fr/" target="_blank" rel="noopener noreferrer" className={`${styles.paragraph_100} ${styles.margincontent}`}>
            <img className={styles.paragraph_100} src={process.env.PUBLIC_URL + '/data/images/ATELIERH.png'} alt="Aperçu du site atelier H" />
        </a>
      <div className={styles.paragraph_100}>
          <p>
              Dans le cadre du projet Atelier H, j'ai principalement travaillé sur l'intégration et l'adaptation de maquettes HTML et CSS au sein de WordPress. Le code HTML initial avait été réalisé par un autre stagiaire. Cependant, il a fallu y apporter des modifications importantes pour répondre aux besoins spécifiques du site et garantir une parfaite compatibilité avec WordPress. J’ai ainsi ajusté la structure et les styles pour qu’ils s'intègrent correctement dans l’écosystème WordPress tout en respectant la mise en page prévue.
          </p>
          <p>
              En parallèle de ces ajustements, j'ai également pris en charge tout le développement back-end. Cela incluait la configuration de plugins, la création de fonctionnalités sur mesure en PHP, et l'optimisation de l’administration WordPress pour une gestion simplifiée du contenu.
          </p>
          <p>
              Au-delà de l’intégration et du développement, j’ai régulièrement corrigé des problèmes techniques, qu’il s’agisse de conflits entre plugins, d’erreurs d’affichage ou de soucis de performance. J’ai dû intervenir pour ajuster le code et et résoudre les bugs afin d’assurer une expérience utilisateur fluide.
          </p>
      </div>
    </div>
  );
}

export default page;